.banner-landing:before {
  content: "";
  width: 55%;
  height: 100%;
  position: absolute;
  bottom: 0;
  right: 0;
  transform-origin: bottom left;
  transform: skew(-30deg, 0deg);
  background: linear-gradient(233deg, #fff 5%, rgba(255, 255, 255, 0.01) 95%);
  mix-blend-mode: soft-light;
  pointer-events: none;
}